	export default {
		data() {
			return {
				addressData: {
					name: '',
					phone: '',
					addressName: '在地图选择',
					address: '',
					area: '',
					def: false
				}
			}
		},
		onLoad(option){
			let title = '新增收货地址';
			if(option.type==='edit'){ 
				this.addressData = JSON.parse(option.data)
			}
			this.manageType = option.type;
			uni.setNavigationBarTitle({
				title
			})
		},
		methods: {
			switchChange(e){
 				this.addressData.def= !this.addressData.def;
			},
			
			//地图选择地址
			chooseLocation(){
				uni.chooseLocation({
					success: (data)=> {
						this.addressData.addressName = data.name;
						this.addressData.address = data.name;
					}
				})
			},
			
			//提交
			confirm(){
				let data = this.addressData;
				if(!data.name){
					this.$api.msg('请填写收货人姓名');
					return;
				}
				if(!/(^1[3|4|5|7|8][0-9]{9}$)/.test(data.phone)){
					this.$api.msg('请输入正确的手机号码');
					return;
				}
				if(!data.address){
					this.$api.msg('请在地图选择所在位置');
					return;
				}
				if(!data.area){
					this.$api.msg('请填写门牌号信息');
					return;
				}
 				let url='save_address';
				if(data.id){
					url='update_address';
				}
				let _this=this;
				this.sendRequest({
					url : url,
					data : data,
					method : "post",
 					success:function (res) {
						if(res.code=='200'){
							//this.$api.prePage()获取上一页实例，可直接调用上页所有数据和方法，在App.vue定义 
							_this.$api.prePage().refreshList(res.data, _this.manageType);
							_this.$api.msg(`地址${this.manageType=='edit' ? '修改': '添加'}成功`);
							setTimeout(()=>{
								uni.navigateBack()
							}, 800)
						}
					}
				}); 
			},
		}
	}
